<template>
  <category class="py-5" id="event">
    <span slot="header">{{ event.name }}</span>

    <event-item v-for="(item, index) in event.items" :key="index" :event="item" :current="index" @show="show_modal($event)" slot="items">
      <share :config="config" :data-title="'嘉兴市和之道心理文化传播中心 -- ' + item.content" :data-image="config.url + item.image"></share>
      <!-- <div class="social-share" data-sites="weibo, wechat" :data-title="item.name" :data-weibo-title="'嘉兴市和之道心理文化传播中心 -- ' + item.content" :data-image="url + item.image" :data-description="item.title"></div> -->
    </event-item>

    <!-- Portfolio Modals -->
    <event-modal :id="'eventModalCenter'" :figure="event.items[current]">
      <div class="modal-footer justify-content-start">
        <p>{{ event.items[current].content }}</p>
      </div>
    </event-modal>
    <!-- /Portfolio Modals -->
  </category>
</template>

<script>
import Category from './Category'
import EventItem from './EventItem'
import EventModal from './Modal'

require('../../node_modules/social-share.js/dist/css/share.min.css')

export default {
  name: 'Event',
  components: {
    EventItem,
    Category,
    EventModal
  },
  data () {
    return {
      event: {items: [{image: ''}]},
      current: 0,
      config: {
        url: 'http://www.jxhzd.tk/',
        sites: ['weibo', 'wechat']
      }
    }
  },
  methods: {
    show_modal (event) {
      const self = this
      self.current = event
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/event.json')
      .then(response => response.json())
      .then(json => {
        self.event = json
      })
  }
}
</script>

<style lang="scss" scoped>
.modal-footer p {
  color: #777;
}
</style>



