<template>
  <div id="footer" class="p-5">
    <div class="container">
      <div class="row">
        <div class="col-md-4">
          <h4>{{ about.name }}</h4>
          <p>{{ about.content }}</p>
        </div>

        <div class="col-md-4">
          <h4>站内导航</h4>
          <nav class="nav">
            <a class="nav-link col-md-6 mb-3 disabled" href="/">首页</a>
            <a class="nav-link col-md-6 mb-3 disabled scroll" :href="'#' + item.id" v-for="(item, index) in navigation" :key="index">{{ item.name }}</a>
          </nav>
        </div>

        <div class="col-md-4">
          <h4>联系地址</h4>
          <address>
            <i class="glyphicon glyphicon-home mr-3" aria-hidden="true"></i> {{ setting.address }}
          </address>
          <address>
            <i class="glyphicon glyphicon-envelope mr-3" aria-hidden="true"></i>
            <a :href="'mailto:' + setting.email">{{ setting.email }}</a>
          </address>

          <figure class="figure mt-3">
            <img :src="setting.wechat" width="120" class="mb-3" />
            <figcaption>
              <h4>官方微信</h4>
            </figcaption>
          </figure>
        </div>
      </div>

      <div class="clearfix"></div>
      <div class="copy">
        <p>Copyright &copy; 2014 - 2017 嘉兴市和之道心理文化传播中心 保留所有权利</p>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'page-footer',
  data () {
    return {
      setting: {},
      navigation: {},
      about: {}
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/setting.json')
      .then(response => response.json())
      .then(json => {
        self.setting = json
      })

    fetch('http://www.jxhzd.tk/api/navigation.json')
      .then(response => response.json())
      .then(json => {
        self.navigation = json
      })
      .then(_ => {
        $('.scroll').click(function (event) {
          event.preventDefault()
          $('html, body').animate({ scrollTop: $(this.hash).offset().top }, 1000)
        })
      })

    fetch('http://www.jxhzd.tk/api/about.json')
      .then(response => response.json())
      .then(json => {
        self.about = json
      })
  }
}
</script>

<style lang="scss" scoped>
#footer {
  background-color: #212121;
}

#footer a,
#footer p,
#footer address {
  color: #999;
  font-size: 1.5rem;
  line-height: 2rem;
}

#footer h4 {
  color: white;
  margin-bottom: 1rem;
  font-size: 2.2rem;
}

#footer .copy {
  padding: 2rem 0;
  border-top: 1px solid #606060;
  text-align: center;
  margin-top: 3rem;
}
</style>



