<template>
  <category class="py-5 container" :id="'about'">
    <span slot="header">{{ about_us.name }}</span>
    <p class="description" slot="description">{{ about_us.content }}</p>

    <div class="col-md-4 text-center my-3" v-for="(item, index) in about_us.items" :key="index" slot="items">
      <figure class="icon rounded-circle position-relative text-center mx-auto mb-4">
        <i class="glyphicon" :class="item.icon" aria-hidden="true"></i>
      </figure>
      <h1 class="mb-3">{{ item.title }}</h1>
      <p>{{ item.content }}</p>
    </div>

    <div class="cotact_us p-2 mb-5 text-center">
      <a class="btn btn-primary btn-lg px-5 py-2" role="button" :href="'mailto:' + setting.email">联系我们</a>
    </div>
  </category>
</template>

<script>
import Category from './Category'

export default {
  name: 'AboutUs',
  components: {
    Category
  },
  data () {
    return {
      about_us: {},
      setting: {
        email: ''
      }
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/setting.json')
      .then(response => response.json())
      .then(json => {
        self.setting = json
      })

    fetch('http://www.jxhzd.tk/api/about.json')
      .then(response => response.json())
      .then(json => {
        self.about_us = json
      })
  }
}
</script>

<style lang="scss" scoped>
@import url("//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-glyphicons.css");

#about .description {
  line-height: 3.5rem;
  text-indent: 4.2rem;
  font: {
    size: 2.5rem;
    family: 'Covered By Your Grace', cursive;
  }
}

.items figure.icon {
  width: 100px;
  height: 100px;
  color: #fff;
  background: #dfdfdf;
  font: {
    size: 5rem;
  }
  line-height: 110px;

  &::before {
    content: '';
    color: #6a67ce;
    width: 102%;
    height: 102%;
    border-radius: 50%;
    background: #6a67ce;
    position: absolute;
    top: -1px;
    left: -1px;
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
    transition: all 0.3s ease 0s;
  }
}

.items>div:hover figure.icon::before {
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}

figure + h1 + p {
  line-height: 2.4rem;
  text-align: left;
  text-indent: 3rem;
  color: #777;
  font: {
    size: 1.8rem;
  }
}

.cotact_us a {
  font: {
    size: 1.5rem;
  }
}
</style>



