<template>
  <div :id="id">
    <h3 class="display-3 font-weight-normal my-4 text-center">
      <slot name="header"></slot>
    </h3>
    <slot name="description"></slot>

    <div class="row items py-4 mx-0">
      <slot name="items"></slot>
    </div>

    <slot></slot>

    <div class="clearfix"></div>
  </div>
</template>

<script>
export default {
  name: 'CategoryTemplate',
  props: ['id']
}
</script>



