<template>
  <div id="baidu_map" class="position-relative">
    <iframe class="w-100 h-100" id="baidu_map" src="static/html/map.html" style="border:0" allowfullscreen></iframe>
    <button type="button" class="btn btn-default position-absolute" @click="reflash()">
      <span class="glyphicon glyphicon-refresh"></span> 在地图上找到我们
    </button>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'baidu_map',
  methods: {
    reflash () {
      $('#baidu_map iframe').attr('src', $('#baidu_map iframe').attr('src'))
    }
  }
}
</script>

<style lang="scss" scoped>
#baidu_map {
  height: 24rem;
}

#baidu_map button {
  right: 1.5rem;
  bottom: 1.5rem;
  font-size: 1.8rem;
}
</style>



