<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
  <a class="navbar-brand py-0" href="#">
    <img src="static/logo.jpg" width="45" height="45" class="d-inline-block align-top" alt="">
  </a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
  <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
    <div class="navbar-nav">
      <a class="nav-item nav-link mx-2 text-white hvr-underline-from-center" href="/">主页 <span class="sr-only">(current)</span></a>
      <a class="nav-item nav-link mx-2 text-white hvr-underline-from-center scroll" :href="'#' + category.id" v-for="(category, index) in categories" :key="index">{{ category.name }}</a>
    </div>
  </div>
</nav>
</template>

<script>
import 'bootstrap'
import $ from 'jquery'

export default {
  name: 'Nav',
  data () {
    return {
      categories: []
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/navigation.json')
      .then(response => response.json())
      .then(json => {
        self.categories = json
        // v_footer.navigation = json;
      })
      .then(_ => {
        $('.scroll').click(function (event) {
          event.preventDefault()
          $('html, body').animate({ scrollTop: $(this.hash).offset().top }, 1000)
        })
      })
  }
}
</script>

<style lang="scss">
@import "~bootstrap/scss/bootstrap";

.navbar-nav>a {
  font: {
    size: 1.5rem;
  }
}
.hvr-underline-from-center {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  overflow: hidden;
}
.hvr-underline-from-center:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 50%;
  right: 50%;
  bottom: 0;
  background: #6a67ce;
  height: 4px;
  -webkit-transition-property: left, right;
  transition-property: left, right;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-underline-from-center:hover:before, .hvr-underline-from-center:focus:before, .hvr-underline-from-center:active:before {
  left: 0;
  right: 0;
}
</style>



