<template>
  <div class="p-0" :class="width">
    <figure class="position-relative m-0 effect-jazz">
     <a href="#galleryModalCenter" data-toggle="modal" @click="$emit('show', current)">
      <img class="w-100 h-100" :src="gallery.image">
      <figcaption class="position-absolute text-white d-flex align-items-center">
       <h4>{{ gallery.title }}</h4>
      </figcaption>
     </a>
    </figure>
  </div>
</template>

<script>

export default {
  name: 'GalleryItem',
  props: ['gallery', 'width', 'current']
}
</script>

<style lang="scss">

figure.effect-jazz {
  overflow: hidden;
  background: -webkit-linear-gradient(-45deg, #000 0%,#000 100%);
  background: linear-gradient(-45deg, #000 0%,#000 100%);
  text-decoration: none;
}

figure.effect-jazz img {
  opacity: 0.9;
}

figure.effect-jazz figcaption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0 10%;
}

figure.effect-jazz figcaption::after,
figure.effect-jazz img,
figure.effect-jazz p,figure.effect-jazz h4 {
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
}

figure.effect-jazz figcaption::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  content: '';
  opacity: 0;
    /*--W3layouts--*/
    /*-Agileits-*/
  -webkit-transform: rotate3d(0,0,1,45deg) scale3d(1,0,1);
  transform: rotate3d(0,0,1,45deg) scale3d(1,0,1);
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

figure.effect-jazz h4,
figure.effect-jazz p {
  opacity: 1;
  text-decoration: none;
  -webkit-transform: scale3d(0.8,0.8,1);
  transform: scale3d(0.8,0.8,1);
}

figure.effect-jazz h4 {
    text-transform: uppercase;
    text-decoration: none;
    font-size: 1.8rem;
    opacity: 0;
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    font-weight: 900;
}

figure.effect-jazz:hover img {
  opacity: 0.5;
  -webkit-transform: scale3d(1.05,1.05,1);
  transform: scale3d(1.05,1.05,1);
}

figure.effect-jazz:hover figcaption::after {
  opacity: 1;
  -webkit-transform: rotate3d(0,0,1,45deg) scale3d(1,1,1);
  transform: rotate3d(0,0,1,45deg) scale3d(1,1,1);
}

figure.effect-jazz:hover h4,
figure.effect-jazz:hover p {
  opacity: 1;
  text-decoration: none;
  -webkit-transform: scale3d(1,1,1);
  transform: scale3d(1,1,1);
}

figure.effect-jazz a {
  text-decoration: none;
}
</style>



