<template>
  <div id="team" class="py-5">
    <category class="container py-0">
      <span slot="header">{{ team.name }}</span>

      <div class="col-sm-4 my-3" v-for="(item, index) in team.items" :key="index" slot="items">
        <figure>
          <img class="w-100" :src="item.image" :alt="item.title">
        </figure>
        <h2 class="text-center text-primary my-3">{{ item.title }}</h2>
      </div>
    </category>
  </div>
</template>

<script>
import Category from './Category'

export default {
  name: 'Team',
  components: {
    Category
  },
  data () {
    return {
      team: {}
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/team.json')
      .then(response => response.json())
      .then(json => {
        self.team = json
      })
  }
}
</script>

<style lang="scss" scoped>
#team {
  background-color: #eee;
}

.items img {
  border: 1px solid #b7b7b7;
}

.items figure {
  position: relative;
  overflow: hidden;
  transition: all 0.3s linear 0s;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.items>div:hover figure {
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
}
.items>div:hover figure::before {
  background: rgba(0,0,0,0.5);
}
</style>



