<template>
  <div class="position-relative" id="home">
    <div class="slider-bg p-4 w-100 h-100">
      <div id="carouselExampleControls" class="carousel slide mx-auto w-100 h-100" data-ride="carousel">
        <div class="carousel-inner mx-auto">
          <div class="card text-white bg-dark carousel-item" :class="{active: index==0}" style="opacity:0.75;" v-for="(slider, index) in sliders.slice()" :key="index">
            <div class="card-header">
              <h1 class="display-5 card-title text-primary text-center mt-3">{{ slider.title }}</h1>
            </div>
            <div class="card-body">
              <p class="card-text font-weight-light" style="font-size: 1.8rem; line-height: 2.4rem; text-indent: 2.4rem;">
                {{ slider.content }}
              </p>
            </div>
          </div>
        </div>
      </div>
      <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Sliders',
  data () {
    return {
      sliders: []
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/sliders.json')
      .then(response => response.json())
      .then(json => {
        self.sliders = json
      })
  }
}
</script>

<style lang="scss">
@import '~bootstrap/scss/_functions';
@import '~bootstrap/scss/_variables';
@import '~bootstrap/scss/mixins/_breakpoints';

#home {
  height: 45rem;
}

.slider-bg {
  background: url('../assets/bg6.jpg') no-repeat center;
}

.carousel-inner {
  top: 1.5rem;
  min-height: 35rem;
}

@include media-breakpoint-up(sm) {
  .carousel-inner {
    top: 8rem;
    max-width: 75%;
    min-height: 30rem;
  }
}

@include media-breakpoint-up(md) {
  .carousel-inner {
    top: 10rem;
    min-height: 25rem;
  }
}

@include media-breakpoint-up(lg) {
  .carousel-inner {
    top: 12rem;
    max-width: 66%;
    min-height: 22rem;
  }
}
</style>



