<template>
  <div class="col-md-4">
    <figure>
      <a href="#" data-toggle="modal" data-target="#eventModalCenter" @click="$emit('show', current)">
        <img class="w-100 h-100" :src="event.image" :alt="event.name"/>
      </a>
    </figure>
    <figcaption class="my-3">
      <p>By an {{ event.author }}, {{ event.created }}</p>
      <p style="color: #777;">{{ event.content }}</p>
    </figcaption>

    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'EventItem',
  props: ['event', 'current']
}
</script>



