<template>
  <a href="#" id="toTop" class="fixed-bottom m-5" @click="scroll_top()">
    <figure class="float-right">
      <img src="../assets/arrow-up-.png">
    </figure>
  </a>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'ScrollTop',
  methods: {
    scroll_top () {
      event.preventDefault()
      $('html, body').animate({ scrollTop: 0 }, 1000)
    }
  }
}
</script>



