<template>
  <category class="py-5 container-fulid" :id="'service'">
    <span slot="header">{{ service.name }}</span>

    <div class="col-md-6 col-lg-3 text-center" v-for="(item, index) in service.items" :key="index" slot="items">
      <figure class="icon mx-auto my-3">
        <img :src="item.icon" :alt="item.title" />
      </figure>
      <h1 class="my-4 text-primary display-5">{{ item.title }}</h1>
      <p>{{ item.content }}</p>
    </div>
  </category>
</template>

<script>
import Category from './Category'

export default {
  name: 'Service',
  components: {
    Category
  },
  data () {
    return {
      service: {}
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/service.json')
      .then(response => response.json())
      .then(json => {
        self.service = json
      })
  }
}
</script>

<style lang="scss" scoped>

#service {
  background: url('../assets/se2.jpg') no-repeat center;
  background-size: cover;
  color: #fff;
}

figure.icon {
  width: 102px;
  height: 102px;
  line-height: 102px;
  position: relative;
  transition: box-shadow 0.2s;

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 0 0 3px rgba(255,255,255,1);
    transition: transform 0.2s, opacity 0.2s;
  }
}
figure.icon:hover {
  box-shadow: 0 0 0 10px rgba(255,255,255,1);
}
figure.icon:hover:after {
  -webkit-transform: scale(0.85);
  -moz-transform: scale(0.85);
  -ms-transform: scale(0.85);
  transform: scale(0.85);
  opacity: 0.5;
}

figure + h1 {
  font: {
    size: 2.2rem;
  }
}
figure + h1 + p {
  line-height: 2rem;
  text-indent: 3rem;
  text-align: left;
  font: {
    size: 1.6rem;
  }
}
</style>



