<template>
  <category class="py-5" id="gallery">
    <span slot="header">{{ gallery.name }}</span>

    <gallery-item v-for="(item, index) in gallery.items.slice(0,3)" :gallery="item" :width="'col-md-4'" :current="index" :key="index" @show="show_modal($event)" slot="items"></gallery-item>
    <gallery-item v-for="(item, index) in gallery.items.slice(3,7)" :gallery="item" :width="'col-md-3'" :current="index + 3" :key="index + 3" @show="show_modal($event)" slot="items"></gallery-item>

    <!-- Portfolio Modals -->
    <gallery-modal :id="'galleryModalCenter'" :figure="gallery.items[current]"></gallery-modal>
    <!-- /Portfolio Modals -->
  </category>
</template>

<script>
import Category from './Category'
import GalleryItem from './GalleryItem'
import GalleryModal from './Modal'

export default {
  name: 'Gallery',
  components: {
    GalleryItem,
    GalleryModal,
    Category
  },
  data () {
    return {
      gallery: {items: [{image: ''}]},
      current: 0
    }
  },
  methods: {
    show_modal (event) {
      const self = this
      self.current = event
    }
  },
  created () {
    const self = this

    fetch('http://www.jxhzd.tk/api/gallery.json')
      .then(response => response.json())
      .then(json => {
        self.gallery = json
      })
  }
}
</script>

<style lang="scss" scoped>
</style>



